import MyInput from '@/components/MyInput/MyInput.vue'
import MyForm from '../../components/MyForm/MyForm.vue'
import MyFormItem from '../../components/MyForm/MyFormItem.vue'
export default {
	data() {
		return {
			model: {
				mail: '',
				pass: ''
			},
			rules: {
				mail: [{
						reg: /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/,
						mode: 'input',
						message: '请输入正确邮箱'
					},
					{
						required: true,
						message: '邮箱不能为空',
						mode: 'blur'
					}
				],
				pass: [{
						required: true,
						message: '密码不能为空',
						mode: 'blur'
					},
					{
						reg: /^[a-zA-Z]{1}([a-zA-Z0-9]|[._]){6,20}$/,
						mode: 'input',
						message: '以字母开头,不能有._以外的符号6-20位'
					}
				]
			}
		}
	},
	components: {
		MyInput,
		MyForm,
		MyFormItem
	},
	methods: {
		async handleClick() {
			console.log('真6')
			let res = await this.$get('/api/getUser');
			console.log(res)
		},
		// 登陆函数
		async submit() {
			let res = await this.$post(`/api/login`, this.model)
			let {data} = res
			if (data.status === 1) {
				console.log(data)
				uni.showToast({
					title: data.data.msg
				})
				uni.setStorageSync('jwt_token', `Bearer ${data.data.token}`);
				uni.setStorageSync('user', this.model.mail)
				uni.switchTab({
					url: '../index/index'
				})
			} else {
				
			}
			console.log(res, 'fasdfasdf=========>>>>>>>>>>>>>>>>>>')
		}
	}
}
